@import url(https://fonts.googleapis.com/css?family=East+Sea+Dokdo&display=swap);
@import url(https://fonts.googleapis.com/css?family=Jua&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

:root {
    --pri-color: white;
}

.errorMsg {
    font-family: 'Jua', sans-serif;
}

.weather_bg {
    z-index: -2;
    position: fixed;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    background-size: 500% 500%;
    -webkit-animation: AnimGradient 60s linear infinite;
            animation: AnimGradient 60s linear infinite;
    opacity: 0;
}

.weather_bg.last {
    -webkit-animation-delay: 30s;
            animation-delay: 30s;
}

[background="Clear"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#cbecf3), color-stop(#acdbeb), color-stop(#d8eaee), color-stop(#f9fbfa), to(#7eaec6));
    background-image: linear-gradient(to bottom right, #cbecf3, #acdbeb, #d8eaee, #f9fbfa, #7eaec6);
}

[background="Haze"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#f7bdb2), color-stop(#f4dcc8), color-stop(#d0e3e8), color-stop(#9aa7ba), to(#9087af));
    background-image: linear-gradient(to bottom right, #f7bdb2, #f4dcc8, #d0e3e8, #9aa7ba, #9087af);
}

[background="Clouds"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#ffffff), color-stop(#cff9ff), color-stop(#c0d7ff), color-stop(#979eff), to(#c0d7ff));
    background-image: linear-gradient(to bottom right, #ffffff, #cff9ff, #c0d7ff, #979eff, #c0d7ff);
}

[background="Mist"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#c5e0ff), color-stop(#aeb9e8), color-stop(#9c97d1), color-stop(#9682ba), to(#aeb9e8));
    background-image: linear-gradient(to bottom right, #c5e0ff, #aeb9e8, #9c97d1, #9682ba, #aeb9e8);
}

[background="Fog"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#8296c0), color-stop(#93a4c9), color-stop(#a5afd2), color-stop(#b7bfdb), to(#c9cfe4));
    background-image: linear-gradient(to bottom right, #8296c0, #93a4c9, #a5afd2, #b7bfdb, #c9cfe4);
}

[background="Thunderstorm"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#323138), color-stop(#5e6469), color-stop(#5b4085), color-stop(#fff975), to(#7f96a7));
    background-image: linear-gradient(to bottom right, #323138, #5e6469, #5b4085, #fff975, #7f96a7);
}

[background="Drizzle"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#ffd69c), color-stop(#db6f02), color-stop(#ff9624), color-stop(#ffb825), to(#ffd69c));
    background-image: linear-gradient(to bottom right, #ffd69c, #db6f02, #ff9624, #ffb825, #ffd69c);
}

[background="Rain"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#bfcdcc), color-stop(#dcd3c3), color-stop(#cbd9d8), color-stop(#e8e4dc), to(#f6f6f3));
    background-image: linear-gradient(to bottom right, #bfcdcc, #dcd3c3, #cbd9d8, #e8e4dc, #f6f6f3);
}

[background="Snow"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#e1f4f4), color-stop(#dce8f9), color-stop(#d5d7f9), color-stop(#d6cdff), to(#c0e6ed));
    background-image: linear-gradient(to bottom right, #e1f4f4, #dce8f9, #d5d7f9, #d6cdff, #c0e6ed);
}

[background="Smoke"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#9ba396), color-stop(#988383), color-stop(#809092), color-stop(#94878d), to(#787a6e));
    background-image: linear-gradient(to bottom right, #9ba396, #988383, #809092, #94878d, #787a6e);
}

[background="Dust"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#8f7680), color-stop(#9e8d9f), color-stop(#9dae9b), color-stop(#7d92a5), to(#605863));
    background-image: linear-gradient(to bottom right, #8f7680, #9e8d9f, #9dae9b, #7d92a5, #605863);
}

[background="Ash"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#ab9582), color-stop(#917d77), color-stop(#7d6a6f), color-stop(#695867), to(#5c4c62));
    background-image: linear-gradient(to bottom right, #ab9582, #917d77, #7d6a6f, #695867, #5c4c62);
}

[background="Squall"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#010326), color-stop(#ACC3F2), color-stop(#365073), color-stop(#6581A6), to(#C2D7F2));
    background-image: linear-gradient(to bottom right, #010326, #ACC3F2, #365073, #6581A6, #C2D7F2);
}

[background="Tornado"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#4ed2ff), color-stop(#3c77b5), color-stop(#606c93), color-stop(#6d5b9d), to(#2096d7));
    background-image: linear-gradient(to bottom right, #4ed2ff, #3c77b5, #606c93, #6d5b9d, #2096d7);
}

[background="Sand"] {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#b8a388), color-stop(#e7d1b5), color-stop(#d8c2a6), color-stop(#ccb598), to(#b8a388));
    background-image: linear-gradient(to bottom right, #b8a388, #e7d1b5, #d8c2a6, #ccb598, #b8a388);
}

@-webkit-keyframes AnimGradient {
    0%{
        background-position:0% 0%;
        opacity: 0;
        z-index: -2;
    }
    5%{
        opacity: 1;
    }
    50%{
        background-position:100% 100%;
        opacity: 1;
        z-index: -3;
    }
    60%{
        opacity: 0;
    }
    100%{
        background-position:0% 0%;
        opacity: 0;
    }
}

@keyframes AnimGradient {
    0%{
        background-position:0% 0%;
        opacity: 0;
        z-index: -2;
    }
    5%{
        opacity: 1;
    }
    50%{
        background-position:100% 100%;
        opacity: 1;
        z-index: -3;
    }
    60%{
        opacity: 0;
    }
    100%{
        background-position:0% 0%;
        opacity: 0;
    }
}

#weather_card {
    font-weight: 400;
    font-family: 'East Sea Dokdo', cursive;
    position: relative;
    background-color: rgba(0, 0, 0, 0.3);
    color: white;
    color: var(--pri-color);
    box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.1);
    border: white 4px solid;
    width: 500px;
    height: 500px;
    margin: auto;
}

#weather_card .temp {
    font-size: 100px;
    margin: 0;
    margin-top: 100px;
}

#weather_card .card_line {
    max-width: 40%;
    margin: 20px auto;
    border-bottom: white 4px dashed;
}

#weather_card .main {
    font-size: 40px;
    margin: 0;
    margin-top: 30px;
}

#weather_card .icon {
    width: 50px;
}

#weather_card .location {
    position: absolute;
    bottom: 10px;
    width: 100%;
    font-size: 25px;
    margin: 0;
}
#fixed_loading_panel {
    background-color: #282c34;
    color: white;
    padding: 0;
    margin: 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
}

#fixed_loading_panel.on{
    opacity: 1;
}

#fixed_loading_panel.off{
    opacity: 0;
}

#fixed_loading_panel > p {
    position: fixed;
    padding: 0;
    margin: 0;
    width: 100%;
    bottom: 20%;
    font-weight: 600;
    font-size: large;
}

.loading_msg {
    padding: 0;
    margin: 0;
    margin-top: -150px;
    position: fixed;
    top: 50%;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.05;
    text-align: center;
    font-size: 200px;
    -webkit-animation: textLight 4s ease-in infinite;
            animation: textLight 4s ease-in infinite;
}

.loader-square .loader {
    position: fixed;
    top: 50%;
    left: 50%;
    margin-top: -100px;
    margin-left: -100px;
    width: 250px;
    height: 250px;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-animation: spinReturn 4s ease infinite;
            animation: spinReturn 4s ease infinite;
}

.loader-square .loader > span {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    border: 10px solid white;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.loader-square .loader > span > span {
    position: absolute;
    top: 20%;
    left: 20%;
    display: block;
    width: 10%;
    height: 10%;
    border: 4px solid white;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-animation: spinReturn 4s ease infinite;
            animation: spinReturn 4s ease infinite;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}

@-webkit-keyframes textLight {
    0% {
        opacity: 0.05;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 0.05;
    }
}

@keyframes textLight {
    0% {
        opacity: 0.05;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 0.05;
    }
}

@-webkit-keyframes spinReturn {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    25% {
        -webkit-transform: rotate(225deg);
                transform: rotate(225deg);
    }
    50% {
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
    }
    75% {
        -webkit-transform: rotate(405deg);
                transform: rotate(405deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spinReturn {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    25% {
        -webkit-transform: rotate(225deg);
                transform: rotate(225deg);
    }
    50% {
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
    }
    75% {
        -webkit-transform: rotate(405deg);
                transform: rotate(405deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
